  handleShipStatus(status) {
    switch (status) {
      case "ORDER_WAIT_RECVGOODS":
        return "等待收货";
      case "ORDER_END_GOODS":
        return "已签收";
      default:
        return status;
    }
  }
  /**
   * 确认收货
   */
  recvGood(item) {
    // expressDetailId
    // console.log(item)
    this.$confirm("确认收货?", "提示", {
      confirmButtonText: "确定",
      cancelButtonText: "取消",
      type: "warning"
    })
      .then(() => {
        let data = {
